export function formatDisplayChatTime(time) {
  const { t } = useI18n('constants.database')
  time = time * 1000

  const date = new Date(time)
  const now = new Date()
  const yesterday = new Date(now)
  yesterday.setDate(yesterday.getDate() - 1)

  const isToday = date.toDateString() === now.toDateString()
  const isYesterday = date.toDateString() === yesterday.toDateString()

  const hours = date.getHours().toString().padStart(2, '0')
  const minutes = date.getMinutes().toString().padStart(2, '0')
  const seconds = date.getSeconds().toString().padStart(2, '0')
  const timeStr = `${hours}:${minutes}:${seconds}`

  if (isToday) {
    return `${t('label_today')} ${timeStr}`
  } else if (isYesterday) {
    return `${t('label_yesterday')} ${timeStr}`
  } else {
    const year = date.getFullYear()
    const month = (date.getMonth() + 1).toString().padStart(2, '0')
    const day = date.getDate().toString().padStart(2, '0')
    return `${year}-${month}-${day} ${timeStr}`
  }
}

/**
 * 
 * @param {string|number} input -  "00:10" 10  "70"
 * @returns {string|number} - 
 */
export function convertTime(input) {
  // 
  if (typeof input === 'number' || (typeof input === 'string' && !isNaN(input))) {
    const minutes = parseInt(input, 10)
    const hours = Math.floor(minutes / 60)
    const remainingMinutes = minutes % 60
    //  padStart 
    return `${String(hours).padStart(2, '0')}:${String(remainingMinutes).padStart(2, '0')}`
  }

  // 
  if (typeof input === 'string') {
    const parts = input.split(':')
    if (parts.length === 2) {
      const hours = parseInt(parts[0], 10) || 0
      const minutes = parseInt(parts[1], 10) || 0
      return hours * 60 + minutes
    }
  }

  // 
  return input
}

