import request from '@/utils/http/axios'

export const getLibraryInfo = ({ id }) => {
  return request.get({
    url: '/manage/getLibraryInfo',
    params: {
      id
    }
  })
}

export const editLibrary = (data) => {
  return request.post({
    url: '/manage/editLibrary',
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    data: data
  })
}

