import { fetchEventSource, EventStreamContentType } from '@/libs/fetch-event-source'
import { useLocaleStoreWithOut } from '@/stores/modules/locale'

export default class SSE {
  onOpen = undefined
  onClose = undefined
  onError = undefined
  onMessage = undefined

  opt = {
    url: '',
    data: {}
  }

  controller = new AbortController()

  constructor(opt = { url: '', data: {} }) {
    this.opt = { ...this.opt, ...opt }

    this.open()
  }

  open() {
    const localeStore = useLocaleStoreWithOut()
    const currentLocale = localeStore.getCurrentLocale

    let formdata = new FormData()

    for (let key in this.opt.data) {
      formdata.append(key, this.opt.data[key])
    }

    let headersData = {
      'App-Type': '',
      'lang': currentLocale.lang,
    }

    if (this.opt.token) {
      headersData = {
        ...headersData,
        'App-Type': '',
        'token': this.opt.token
      }
    }

    fetchEventSource(this.opt.url, {
      method: 'POST',
      headers: headersData,
      signal: this.controller.signal,
      // ()
      openWhenHidden: true,
      body: formdata,
      onopen: async(response) => {
        if (response.ok && response.headers.get('content-type') === EventStreamContentType) {
          if (typeof this.onOpen === 'function') {
            this.onOpen()
          }
          return // everything's good
        }
        
        throw new Error(`: ${response.status} ${response.statusText}`);
      },
      onmessage: (res) => {
        if (typeof this.onMessage === 'function') {
          this.onMessage(res)
        }
      },
      onclose: () => {
        if (typeof this.onClose === 'function') {
          this.onClose()
        }

        this.abort();
      },
      onerror: (err) => {
        console.log(err)
        // 
        if (typeof this.onError === 'function') {
          this.onError(err)
        }

        this.abort();
        throw err
      }
    })
  }

  abort = () => {
    //  abort 
    if (!this.controller.signal.aborted) {
      this.controller.abort()
    }
  }
}
