import { checkPermi } from '@/utils/permission'

function checkPermission(el, binding) {
  const { value } = binding

  if (value && Array.isArray(value) && value.length > 0) {
    if (!checkPermi(value)) {
      el.parentNode && el.parentNode.removeChild(el)
    }
  } else {
    throw new Error('')
  }
}

export default {
  mounted(el, binding) {
    checkPermission(el, binding)
  }
  // updated(el, binding) {
  //   checkPermission(el, binding)
  // }
}
