import { defineStore } from 'pinia'
import { getCompany } from '@/api/user'
const defaultAvatar = 'https://xkf-upload-oss.xiaokefu.com.cn/static/chat-wiki/favicon.ico'
const topNavigateDefaultData = [
  {
    id: 'explore',
    name: '',
    open: true,
    isDisabled: true,
  },
  {
    id: 'robot',
    name: '',
    open: true,
    isDisabled: true,
  },
  {
    id: 'library',
    name: '',
    open: true,
    isDisabled: true,
  },
  {
    id: 'PublicLibrary-new',
    name: '',
    open: false
  },
  {
    id: 'library-search-new',
    name: '',
    open: false
  },
  {
    id: 'chat-monitor',
    name: '',
    open: true
  },
  // {
  //   id: 'user',
  //   name: '',
  //   open: true
  // }
]

export const useCompanyStore = defineStore('company', {
  state: () => {
    return {
      name: '',
      id: '',
      companyInfo: undefined,
      ali_ocr_switch: '2',
      ali_ocr_key: '',
      ali_ocr_secret: '',
      is_public_network: '',
      topNavigateDefaultData: topNavigateDefaultData,
      top_navigate: topNavigateDefaultData,
    }
  },
  getters: {

  },
  actions: {
    async getCompanyInfo() {
      const res = await getCompany()

      if (!res) {
        return Promise.reject(res)
      }

      this.setCompanyInfo(res.data)

      return res
    },

    setCompanyInfo(data) {
      this.companyInfo = data;
      this.name = data ? data.name : '';
      this.id = data ? data.id : '';
      this.ali_ocr_switch = data ? data.ali_ocr_switch : '';
      this.ali_ocr_key = data ? data.ali_ocr_key : '';
      this.ali_ocr_secret = data ? data.ali_ocr_secret : '2';
      this.is_public_network = data ? data.is_public_network : '';
      if(data && data.top_navigate){
        let top_navigate = JSON.parse(data.top_navigate)
        this.top_navigate = topNavigateDefaultData.map(item => {
          let findItem =  top_navigate.find(it => it.id == item.id)
          return findItem ? findItem : item
        })
      }else{
        this.top_navigate = topNavigateDefaultData
      }
      setFavicon(data?.avatar || defaultAvatar)
    },
  },
})


function setFavicon(url) {
  // 1.  favicon 
  const existingLinks = document.querySelectorAll('link[rel="icon"], link[rel="shortcut icon"]');
  existingLinks.forEach(link => link.remove());

  // 2.  favicon 
  const link = document.createElement('link');
  link.rel = 'icon';
  link.type = 'image/x-icon';
  link.href = url;

  // 3. 
  document.head.appendChild(link);
}
